.field-text {
	position: relative;
	overflow: hidden;
	display: flex;
	background: rgba(var(--shading), .1);
	border-radius: 3px;
}

.field-text__input {
	flex-grow: 1;
	box-sizing: content-box;
	width: auto;
	height: var(--field-height);
	padding: var(--field-padding);
	outline: none;
	border: none;
	background: transparent;
	color: inherit;
}
