.field-set {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	align-items: stretch;
	gap: 1rem;
	padding: 1rem;
	--field-height: 1.5rem;
	--field-padding: .7rem;
}

.field-set__label {
	display: block;
	margin: 0 0 .2rem 0;
	font-size: .8rem;
	color: rgba(var(--text), .8);
	transition: color .2s linear;
}

.field-set__field:focus-within > .field-set__label {
	color: rgb(var(--accent));
}
