.box-decorator {
	position: absolute;
	bottom: 0;
	right: 0;
	left: 0;
	height: 1px;
	background: rgb(var(--accent));
	opacity: 0;
	transform: scale3d(0, 1, 1);
	transform-origin: 0% 0%;
	visibility: hidden;
	transition:
		opacity .2s linear,
		transform .7s cubic-bezier(.2, 1, 0, 1) .2s,
		visibility 0s linear .2s;
}

*:focus-within > .box-decorator {
	opacity: 1;
	transform: scale3d(1, 1, 1);
	visibility: visible;
	transition-delay: 0s;
}
